.layout-menu {
  background-color: grey;
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 2000;
  width: fit-content;
  box-sizing: border-box;
  border-bottom-left-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;
  overflow: hidden;
  display: flex;
  gap: var(--spacing, 1rem);
  align-items: center;
  padding: 0;
}
.layout-menu p {
  padding: 0;
  margin: 0;
}
