.layout-handle {
  opacity: 0;
  position: absolute;
  z-index: 10;
  top: 0;
  right: 1rem;
  left: 1rem;
  transition: 150ms opacity;
  display: flex;
  gap: 2rem;
  justify-content: space-between;
  pointer-events: none;
}
